/* Adjusted styles to handle user list not defined error */

.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding: 20px;
  box-sizing: border-box;
  position: relative; /* Added to ensure user list positioning */
}

.message-container {
  flex-grow: 1;
  overflow-y: auto;
}

.chat-input-container {
  position: relative;
  margin-top: 20px;
}

.chat-input {
  flex-grow: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.send-button,
.emoji-button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.emoji-button {
  margin-left: 10px;
}

.user-list {
  position: absolute;
  top: calc(100% + 5px); /* Adjusted to prevent overlap with input */
  left: 0;
  z-index: 1;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  max-height: 150px;
  overflow-y: auto;
  width: calc(100% - 2px); /* Adjust for border */
}

.user {
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.user:hover {
  background-color: #f0f0f0;
}